html[data-theme='dark'] {
    --bg: #333333;
    --bg-panel: #434343;
    --color-headings: #3694FF;
    --color-text: #B5B5B5;
}

html {
  font-family: 'Crimson Text', serif;
  text-align: center;
  font-size: 22px;
  padding: 2.5%;
  color: #001B3A;
}
h1, h2 , h3, h4, h5 {
  font-family: 'Source Serif Pro', serif;
}

a:link {
  color: black;
}
a:active {
  color: black;
}

.bio {
  padding-left: 30%;
  text-align: left;
}

input[type=checkbox]{
	height: 0;
	width: 0;
	visibility: hidden;
}
label {
	cursor: pointer;
	text-indent: -9999px;
	width: 52px;
	height: 27px;
	background: grey;
	float: right;
	border-radius: 100px;
	position: relative;
}

label:after {
	content: '';
	position: absolute;
	top: 3px;
	left: 3px;
	width: 20px;
	height: 20px;
	background: #fff;
	border-radius: 90px;
	transition: 0.3s;
}
